import React, { useRef } from "react";
import {
  Flex,
  Tooltip,
  IconButton,
  Box,
  AlertDialog,
  AlertDialogOverlay,
  AlertDialogContent,
  AlertDialogHeader,
  AlertDialogBody,
  AlertDialogFooter,
  Button,
  useDisclosure,
} from "@chakra-ui/react";
import { FaInstagram, FaSnapchatGhost, FaTiktok } from "react-icons/fa";
import { IoLogOut } from "react-icons/io5";
import { useRouter } from "next/router";

const Sidebar = () => {
  const router = useRouter();
  const { isOpen, onOpen, onClose } = useDisclosure();
  const cancelRef = useRef<HTMLButtonElement>(null);

  const handleLogout = () => {
    onClose();
    router.push("/login");
  };

  const navItems = [
    { icon: FaInstagram, label: "Instagram", href: "/instagram" },
    { icon: FaSnapchatGhost, label: "Snapchat", href: "/snapchat" },
    { icon: FaTiktok, label: "TikTok", href: "/tiktok" },
  ];

  return (
    <>
      <Flex
        direction="column"
        align="center"
        bg="gray.800"
        p={4}
        width="80px"
        height="100vh"
        boxShadow="lg"
      >
        {navItems.map((item, index) => (
          <React.Fragment key={item.label}>
            <Tooltip label={item.label} aria-label={item.label}>
              <IconButton
                icon={<item.icon size="24px" />}
                aria-label={item.label}
                variant="ghost"
                mb={2}
                _hover={{ color: "teal.300" }}
                onClick={() => router.push(item.href)}
              />
            </Tooltip>
            {index < navItems.length && (
              <Box height="1px" width="20px" bg="gray.400" mb={2} />
            )}
          </React.Fragment>
        ))}

        {/* Logout Icon */}
        <Tooltip label="Logout" aria-label="Logout">
          <IconButton
            icon={<IoLogOut style={{ transform: "scaleX(-1)" }} />}
            aria-label="Logout"
            variant="ghost"
            mt="auto"
            _hover={{ color: "red.400" }}
            onClick={onOpen}
          />
        </Tooltip>
      </Flex>

      {/* Logout Confirmation Dialog */}
      <AlertDialog
        isOpen={isOpen}
        leastDestructiveRef={cancelRef}
        onClose={onClose}
      >
        <AlertDialogOverlay>
          <AlertDialogContent>
            <AlertDialogHeader fontSize="lg" fontWeight="bold">
              Confirm Logout
            </AlertDialogHeader>

            <AlertDialogBody>
              Are you sure you want to log out?
            </AlertDialogBody>

            <AlertDialogFooter>
              <Button ref={cancelRef} onClick={onClose}>
                Cancel
              </Button>
              <Button colorScheme="red" onClick={handleLogout} ml={3}>
                Logout
              </Button>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialogOverlay>
      </AlertDialog>
    </>
  );
};

export default Sidebar;
